import React from "react";
import {Menu} from "antd";
import Sider from "antd/es/layout/Sider";
import {NavLink} from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faBirthdayCake } from '@fortawesome/free-solid-svg-icons'
import { faCandyCane } from '@fortawesome/free-solid-svg-icons'
import { faAddressCard } from '@fortawesome/free-solid-svg-icons'
import { faCookie } from '@fortawesome/free-solid-svg-icons'


export const SiderApp = () => {
    return <Sider width={200} className="site-layout-background">
        <Menu
            mode="inline"
            defaultSelectedKeys={['1']}
            defaultOpenKeys={['sub1']}
            style={{height: '100%', borderRight: 0}}
        >
            <Menu.Item key="1"><FontAwesomeIcon style={{marginRight: 10}} icon={faBirthdayCake} /><NavLink to={'/cakes'}>Торты</NavLink></Menu.Item>
            <Menu.Item key="2"><FontAwesomeIcon  style={{marginRight: 10}} icon={faCookie} /><NavLink to={'/baking'}>Выпечка</NavLink></Menu.Item>
            <Menu.Item key="3"><FontAwesomeIcon style={{marginRight: 10}}  icon={faCandyCane} /><NavLink to={'/candybar'}>Кэнди Бар</NavLink></Menu.Item>
            <Menu.Item key="4"><FontAwesomeIcon style={{marginRight: 10}}  icon={faAddressCard} /><NavLink to={'/aboutme'}>О Себе</NavLink></Menu.Item>
        </Menu>
    </Sider>
}