import React from "react";
import AwesomeSlider from 'react-awesome-slider';
import 'react-awesome-slider/dist/styles.css';
import cake1 from '../../assets/cakes/1.jpg'
import cake2 from '../../assets/cakes/2.jpg'
import cake3 from '../../assets/cakes/3.jpg'
import cake4 from '../../assets/cakes/4.jpg'
import cake5 from '../../assets/cakes/5.jpg'
import cake6 from '../../assets/cakes/6.jpg'
import cake7 from '../../assets/cakes/7.jpg'
import cake8 from '../../assets/cakes/8.jpg'
import cake9 from '../../assets/cakes/9.jpg'
import cake10 from '../../assets/cakes/10.jpg'
import cake11 from '../../assets/cakes/11.jpg'




export const CakesApp = () => {
    return <div >
        <AwesomeSlider style={{width: 1024, height: 768, margin: '0 auto'}}>
            <div data-src={cake1} />
            <div data-src={cake2} />
            <div data-src={cake3} />
            <div data-src={cake4} />
            <div data-src={cake5} />
            <div data-src={cake6} />
            <div data-src={cake7} />
            <div data-src={cake8} />
            <div data-src={cake9} />
            <div data-src={cake10} />
            <div data-src={cake11} />
        </AwesomeSlider>
    </div>
}




