import React from "react";
import AwesomeSlider from 'react-awesome-slider';
import 'react-awesome-slider/dist/styles.css';
import baking1 from '../../assets/baking/1.jpg'
import baking2 from '../../assets/baking/2.jpg'
import baking3 from '../../assets/baking/3.jpg'
import baking4 from '../../assets/baking/4.jpg'
import baking5 from '../../assets/baking/5.jpg'
import baking6 from '../../assets/baking/6.jpg'
import baking7 from '../../assets/baking/7.jpg'
import baking8 from '../../assets/baking/8.jpg'




export const BakingApp = () => {
    return <div >
        <AwesomeSlider style={{width: 1024, height: 768, margin: '0 auto'}}>
            <div data-src={baking1} />
            <div data-src={baking2} />
            <div data-src={baking3} />
            <div data-src={baking4} />
            <div data-src={baking5} />
            <div data-src={baking6} />
            <div data-src={baking7} />
            <div data-src={baking8} />
        </AwesomeSlider>
    </div>
}




