import React from "react";
import AwesomeSlider from 'react-awesome-slider';
import 'react-awesome-slider/dist/styles.css';
import candybar1 from '../../assets/candy-bar/1.jpg'
import candybar2 from '../../assets/candy-bar/2.jpg'
import candybar3 from '../../assets/candy-bar/3.jpg'




export const CandyBarApp = () => {
    return <div >
        <AwesomeSlider style={{width: 1024, height: 768, margin: '0 auto'}}>
            <div data-src={candybar1} />
            <div data-src={candybar2} />
            <div data-src={candybar3} />
        </AwesomeSlider>
    </div>
}