import React from 'react';
import './App.css';
import {Layout} from 'antd';
import {HeaderApp} from "./components/Header/Header";
import {SiderApp} from "./components/Sider";
import {Content} from "antd/es/layout/layout";
import {BrowserRouter, Route} from "react-router-dom";
import {CakesApp} from "./components/MainPages/Cakes";
import {BakingApp} from "./components/MainPages/Baking";
import {CandyBarApp} from "./components/MainPages/CandyBar";
import {AboutMeApp} from "./components/MainPages/AboutMe";


function App() {
  return (
      <BrowserRouter>
          <Layout>
              <HeaderApp/>
              <Layout>
                  <SiderApp/>
                  <Layout style={{ padding: '0 24px 24px' }}>
                      <Content
                          className="site-layout-background"
                          style={{
                              padding: 24,
                              margin: 0,
                              minHeight: 280,
                          }}
                      >
                          <Route path="/cakes" render={() =>
                              <CakesApp/>}/>
                          <Route path="/baking" render={() =>
                              <BakingApp/>}/>
                          <Route path="/candybar" render={() =>
                              <CandyBarApp/>}/>
                          <Route path="/aboutme" render={() =>
                              <AboutMeApp/>}/>
                      </Content>
                  </Layout>
              </Layout>
          </Layout>
      </BrowserRouter>
  );
}

export default App;
